"use client";
import Link from "next/link";
import { useState, useEffect } from "react";

export default function Home() {
  const [count, setCount] = useState<number | null>(null);

  useEffect(() => {
    const savedValue = window.localStorage.getItem("count");
    setCount(savedValue ? Number(savedValue) : 0);
  }, []);

  useEffect(() => {
    if (typeof count === "number") {
      window.localStorage.setItem("count", String(count));
    }
  }, [count]);

  return (
    <main className="flex min-h-screen flex-col items-center justify-between p-24">
      <div className="w-full max-w-5xl">
        <div className="flex justify-between items-center mb-8">
          <h1 className="text-2xl font-bold">Template Management</h1>
          <div className="flex items-center gap-4">
            <button
              onClick={() =>
                setCount((prev) => (typeof prev === "number" ? prev + 1 : 1))
              }
              className="bg-gray-200 hover:bg-gray-300 text-gray-800 font-bold py-2 px-4 rounded"
            >
              Count: {typeof count === "number" ? count : <span>...</span>}
            </button>
            <Link href="/form-builder">
              <button className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded">
                Create New Form
              </button>
            </Link>
          </div>
        </div>
        <div>
          {/* Placeholder for the list of templates */}
          <p>List of templates will be displayed here.</p>
        </div>
      </div>
    </main>
  );
}
